.navbar{
    padding: 20px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.navbar logo{
    width: 150px;
 
}

.navbar-menu{
    display: flex;
    gap: 20px;
    list-style: none;
    color: #49557e;
    font-size:18px ;

}
.navbar-right{
    display: flex;
    gap: 40px;
    align-items: center;
}
.navbar button{
    background: transparent;
    font-size: 16px;
    color: #49557e;
    border: 1px solid tomato;
    padding: 10px 30px;
    border-radius: 50px; 
    cursor: pointer;
    transition:  0.3 ;
}
.navbar button:hover{
    background: #49557e;
    color: white;
}
.navbar .active{
    padding-bottom:2px;
    border-bottom: 2px solid #49557e;
}
.navbar .li{
    cursor:pointer

}

.navbar-search-icon{
    position: relative;

}

.navbar-search-icon .dot{
    position: absolute;
    min-width: 10px;
    min-height: 10px;
    background-color: tomato;
    border-radius: 5px ;
    top:-8px;
    right: -8px;
}

    

@media (max-width:1050px) {
    .navbar{
        width: 140px;

    }
    .navbar-menu{
        gap: 20px;
    }
    .navbar-right{
        gap: 30px;
    }
    .navbar-right img{
        padding: 8px 25px;
    }
    
}
@media (max-width:900px) {
    .navbar{
        width: 120px;

    }
    .navbar-menu{
        gap: 15px;
        font-size: 16px;
    }
    .navbar-right{
        gap: 20px;
    }
    .navbar-right img{
        padding: 7px 20px;
        font-size: 15px;
    }
    
}

@media (max-width:750px) {
  
    .navbar-menu{
        display: none;
    }
  
}